[class^="mif-"],
[class*=" mif-"] {
  display: inline-block;
  font: normal normal normal 1.2em/1 metro;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: middle;
}
.mif-lg {
